import { Logger } from "../Util/Logger";
import { Unit, UnitArgs } from "./Unit";

export abstract class TickElement extends Unit
{
    private tickEvent: number;
    
    /**
     * @inheritDoc
     */
    public Init(args: UnitArgs = {})
    {
        super.Init(args);
    }

    /**
     * @inheritDoc
     */
    protected InitPost(args: UnitArgs = {})
    {
        super.InitPost(args);

        // Start to listen to the tick event
        this.tickEvent = this.board.OnTick.Add(() => this.OnTick());

        Logger.Info(this, "Tick event was set", this);
    }

    /**
     * @inheritDoc
     */
    public Dispose(value: boolean = true)
    {
        this.board.OnTick.Remove(this.tickEvent);
        super.Dispose(value);
    }
    
    /**
     * Called upon tick.
     */
    protected abstract OnTick(): void;
}