import { Board } from "../lib/Board";
import { Coord } from "../lib/Coord";
import { GroundCell } from '../lib/Element/Cell/GroundCell';
import { StoneCell } from '../lib/Element/Cell/StoneCell';
import { PlayerActor } from '../lib/Element/Actor/PlayerActor';
import { Server } from '../lib/Net/Server';
import { Renderer } from "../lib/Renderer";
import { Keyboard } from "../lib/Util/Keyboard";
import { Sender } from "../lib/Net/Sender";
import { FakeChannel } from "../lib/Net/Channel/FakeChannel";
import { Receiver } from "../lib/Net/Receiver";
import { PeerChannel } from "../lib/Net/Channel/PeerChannel";
import { Tools } from "../lib/Util/Tools";
import { Exportable } from "../lib/Exportable";
import { Http } from "../lib/Util/Http";
import { ArrowActor } from "../lib/Element/Actor/ArrowActor";
import { FireCell } from "../lib/Element/Cell/FireCell";
import { WaterCell } from "../lib/Element/Cell/WaterCell";

// Register classes as a dependency
Exportable.Register(ArrowActor);
Exportable.Register(PlayerActor);
Exportable.Register(FireCell);
Exportable.Register(GroundCell);
Exportable.Register(StoneCell);
Exportable.Register(WaterCell);
Exportable.Register(Board);
Exportable.Register(Coord);

// HTML elements
let _gameCanvas: HTMLCanvasElement;
let _addButton: HTMLButtonElement;
let _messageDiv: HTMLDivElement;

// Tab ID
let _tabId: string;

// Game objects
let _board: Board;

// For client or _server
let _channel: PeerChannel;
let _server: Server;

// Last shoot
export let nextShoot = +new Date(0);

/**
 * Type of the hash format.
 */
enum HashType
{
    Offer,
    Answer
}

/**
 * Structure of the hash string.
 */
interface HashFormat
{
    Tab: string;
    Type: HashType,
    Payload: string;
}

/**
 * Set a message on the screen.
 * @param message 
 */
export const SetMessage = (message: string): void =>
{
    _messageDiv.innerText = message;
}

/**
 * Copy text to clipboard.
 * @param text 
 */
export const ClipboardCopy = async (text: string): Promise<void> =>
{
    const success = await Tools.Clipboard(text);

    if(!success)
    {
        prompt("", text);
    }
}

/**
 * Construct a new URL from HashFormat.
 * @param hashFormat 
 */
export const ConstructUrl = (hashFormat: HashFormat): string =>
{
    return location.origin + location.pathname + "#" + 
        encodeURI(btoa(JSON.stringify(hashFormat)));
};

/**
 * Read the location hash.
 */
export const ReadHash = (): HashFormat =>
{
    try 
    {
        return JSON.parse(atob(decodeURI(location.hash.substr(1))));
    }
    catch(e)
    {
        return {
            Tab: null,
            Type: null,
            Payload: null
        };
    }
};

/**
 * Create an offer.
 */
export const ClickAdd = async (): Promise<void> =>
{
    if(!_server)
    {
        return;
    }

    const channel = new PeerChannel();
    const offer = await channel.Offer();
    const url = ConstructUrl({
        Tab: _tabId,
        Type: HashType.Offer,
        Payload: offer
    });
    
    ClipboardCopy(url);
    SetMessage("Offer copied to clipboard!");

    channel.OnOpen = () => 
    {
        SetMessage("A new player joined!");
        _server.Add(new Sender(channel, _server));
    };

    while(true)
    {
        const answer = localStorage.getItem(_tabId);

        if(answer)
        {
            channel.Finish(answer);
            localStorage.removeItem(_tabId);
            break;
        }

        await Tools.Wait(1000);
    }
};

/**
 * Create receiver (and _server).
 */
export const CreateReceiver = async (renderer: Renderer): Promise<Receiver> =>
{
    if(_channel && !_channel.IsOfferor())
    {
        return new Receiver(_channel, _board);
    }

    // Create _server _board, load it, create _server
    try 
    {
        const rawboard = JSON.parse(await Http.Get("res/board.json"));
        const serverboard = Exportable.Import(rawboard);

        _server = new Server(serverboard);

        // Use the tick of the local client on the _server
        renderer.OnDraw.Add(() => serverboard.OnTick.Call());
    }
    catch(e)
    {
        return null;
    }

    // Show add button
    _addButton.style.display = "block";

    // Create a fake _channel
    const localA = new FakeChannel();
    const localB = new FakeChannel();

    localA.SetOther(localB);
    localB.SetOther(localA);

    // Add connection to the _server
    _server.Add(new Sender(localA, _server));

    // Connect client to the _server
    return new Receiver(localB, _board);
}

/**
 * Game cycle
 * @param player 
 * @param data.up
 * @param data.left
 * @param data.down
 * @param data.right
 * @param data.space
 */
export const OnDraw = (player: PlayerActor, { up, left, down, right, space }) =>
{
    if(!player)
    {
        return;
    }

    const direction = new Coord(
        Keyboard.Keys[left] ? -1 : Keyboard.Keys[right] ? 1 : 0, 
        Keyboard.Keys[up] ? -1 : Keyboard.Keys[down] ? 1 : 0
    );

    if(direction.GetDistance(new Coord) == 1)
    {
        player.Move(direction);
    }

    if(Keyboard.Keys[space] && nextShoot <= +new Date)
    {
        player.Shoot(Tools.Unique());
        nextShoot = +new Date + 1000;
    }
};

/**
 * Start game.
 */
const Start = async () =>
{
    Keyboard.Init();

    const renderer = new Renderer(_board, _gameCanvas);
    const receiver = await CreateReceiver(renderer);

    receiver.OnPlayer = async player =>
    {
        _board.Origin = player.Id;

        await renderer.Load();
        
        const keys = 
        {
            up: "ARROWUP", 
            left: "ARROWLEFT", 
            down: "ARROWDOWN", 
            right: "ARROWRIGHT",
            space: " "
        };

        renderer.OnDraw.Add(() => OnDraw(player, keys));
        renderer.Start();
    };
};

/**
 * Main function.
 */
const Main = async (): Promise<void> =>
{
    const hash = ReadHash();

    // If it is an offer, create an answer and wait for an open _channel.
    if(hash.Type == HashType.Offer)
    {
        _channel = new PeerChannel();
        _channel.OnOpen = () => Start();

        const answer = await _channel.Answer(hash.Payload);
        const url = ConstructUrl({
            Tab: hash.Tab,
            Type: HashType.Answer,
            Payload: answer
        });

        ClipboardCopy(url);
        SetMessage("Answer copied to clipboard!");
    }

    // If it is an answer, give it to the _server tab using the local storage
    else if(hash.Type == HashType.Answer)
    {
        localStorage.setItem(hash.Tab, hash.Payload);
        SetMessage("You can close this tab!");
    }

    // If no hash is present, start the game
    else
    {
        Start();
    }
};

if (document.currentScript.getAttribute("src").includes("index")) 
{
    _gameCanvas = <HTMLCanvasElement>document.getElementById("game-canvas");
    _addButton = <HTMLButtonElement>document.getElementById("add-button");
    _messageDiv = <HTMLDivElement>document.getElementById("message-div");

    _addButton.onclick = () => ClickAdd();

    _board = new Board();
    _tabId = Tools.Unique();

    document.onload = Main;
}